type Exercise = {
  id: number;
  name: string;
  weight: number;
  sets: number;
  reps: number;
};

type Workout = {
  id: number;
  userId: string;
  name: string;
  createdAt: string;
  exercises: Exercise[];
};

type WeekPlannerUIProps = {
  workoutsByDay: { [key: string]: Workout[] };
};

export function WeekPlannerUI({ workoutsByDay }: WeekPlannerUIProps) {
  // Render the UI based on workoutsByDay
  return (
    <div>
      {Object.entries(workoutsByDay).map(([day, workouts]) => (
        <div key={day}>
          <h2>{day}</h2>
          {workouts.map((workout) => (
            <div key={workout.id}>
              <h3>{workout.name}</h3>
              {workout.exercises.map((exercise) => (
                <p key={exercise.id}>
                  {exercise.name}: {exercise.weight}kg {exercise.sets} sets of {exercise.reps} reps
                </p>
              ))}
            </div>
          ))}
        </div>
      ))}
    </div>
  );
}