import { getWorkoutFromDay, getExercisesFromWorkout } from "@/app/actions";
import { WeekPlannerUI } from "./WeekPlannerUI";

type Exercise = {
  id: number;
  name: string;
  weight: number;
  sets: number;
  reps: number;
};

type Workout = {
  id: number;
  userId: string;
  name: string;
  createdAt: string;
  exercises: Exercise[];
};

const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];

export default async function WeekPlanner() {
  const userId = "557827b2-a6e2-49b9-9016-1d3bcf6a6422";

  const workoutsByDay = await Promise.all(
    days.map(async (day) => {
      const workouts = await getWorkoutFromDay(userId, day);
      const workoutsWithExercises = await Promise.all(
        workouts.map(async (workout) => ({
          ...workout,
          exercises: await getExercisesFromWorkout(workout.id),
        }))
      );
      return { day, workouts: workoutsWithExercises };
    })
  );

  const workoutsData: { [key: string]: Workout[] } = Object.fromEntries(
    workoutsByDay.map(({ day, workouts }) => [day, workouts])
  );

  return <WeekPlannerUI workoutsByDay={workoutsData} />;
}